/* eslint-disable */
import react from 'react'
import { Cell, Cascader } from '@antmjs/vantui'

export default function Demo() {
  const [isVisiblevalue, setIsVisiblevalue] = react.useState(false)
  const [value, setValue] = react.useState(['', ''])
  const [optionsvalue] = react.useState([
    { value: '', text: '', id: 1, pid: null },
    { value: '', text: '', id: 11, pid: 1 },
    { value: '', text: '', id: 111, pid: 11 },
    { value: '', text: '', id: 2, pid: null },
    { value: '', text: '', id: 21, pid: 2 },
  ])
  const [convertConfigvalue] = react.useState({
    topId: null,
    idKey: 'id',
    pidKey: 'pid',
    sortKey: '',
  })
  const change = (value) => {
    setValue(value)
  }

  return (
    <>
      <Cell
        title=""
        value={value ? value.join('-') : ''}
        onClick={() => {
          setIsVisiblevalue(true)
        }}
      ></Cell>
      <Cascader
        visible={isVisiblevalue}
        value={value}
        title=""
        options={optionsvalue}
        convertConfig={convertConfigvalue}
        closeable
        onClose={() => {
          setIsVisiblevalue(false)
        }}
        onChange={change}
      />
    </>
  )
}
